.programs {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: $desktop-width) {
    background-image: url("../img/programs/prog_bg_desk.png");
    background-size: 680px 426px;
    background-position: 686px 52px;
    background-repeat: no-repeat;
    padding-top: 100px;
  }
}

.programs__container {
  display: grid;
  overflow: hidden;
  grid-template-columns: 320px 320px 320px;
  grid-template-rows: 55px 80px auto;
  background-image: url("../img/programs/prog_bg_mob.png");
  background-size: 320px 201px;
  background-position: 329px 117px;
  background-repeat: no-repeat;


  @media (min-width: $desktop-width) {
    grid-template-columns: 278px 112px 560px;
    grid-template-rows: 120px 342px auto;
    background-image: none;
  }

  h2 {
    grid-column: 2 / 3;
    letter-spacing: -0.7px;

    @media (min-width: $desktop-width) {
      grid-column: 1 / 2;
      margin-bottom: 0;
    }
  }
}

.programs__list {
  position: relative;
  display: grid;
  grid-template-columns: repeat(5, 20%);
  gap: 8px;
  grid-column: 1 / -1;

  @media (min-width: $desktop-width) {
    grid-template-rows: repeat(5, 20%);
    grid-template-columns: 228px;
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    gap: 0;
    margin-left: 2px;
  }
}

.programs__button {
  position: relative;
  display: block;
  width: 181px;
  height: 48px;
  font-weight: 400;
  font-size: 15px;
  opacity: 0.87;
  color: $black;
  line-height: 18px;

  &:hover {
    color: $yellow;
  }

  &:active {
    @include active-button;

    @media (min-width: $desktop-width) {
      @include active-button-desk;
    }
  }

  &::before {
    position: absolute;
    content: "";
    top: 8px;
    left: 12px;
    width: 32px;
    height: 32px;
    background-size: contain;
    background-repeat: no-repeat;
    opacity: 0.7;
  }

  @media (min-width: $desktop-width) {
    width: 228px;
    height: 64px;
    padding-top: 15px;
  }
}

.programs__button--active {
  @include active-button;

  &::first-letter {
    text-transform: lowercase;
  }

  &::before {
    opacity: 1;
  }

  &:hover {
    color: $white;
  }

  @media (min-width: $desktop-width) {
    @include active-button-desk;

    height: 68px;
    padding-top: 0;
    font-size: 16px;
    line-height: 19px;
  }
}

.programs__academic {
  padding-left: 40px;

  &::before {
    background-image: url("../img/programs/noun_582688_cc.png");
    background-size: 32.36px 31.46px;

    @media (min-width: $desktop-width) {
      background-size: 36px 35px;
      width: 36px;
      height: 35px;
      top: 16px;
      left: 20px;
    }
  }

  &:hover {
    &::before {
      background-image: url("../img/programs/1_g.png");
    }
  }

  &:active {
    &::before {
      background-image: url("../img/programs/noun_582688_cc-1.png");
    }
  }
}

.programs__academic.programs__button--active {
  &::before {
    background-image: url("../img/programs/noun_582688_cc-1.png");
  }
}

