.programs__description {
  @include list-reset;
  grid-column: 1 / -1;
  padding-right: 16px;
  padding-left: 16px;
  width: 100vw;
  margin-right: auto;
  margin-left: auto;

  @media (min-width: $desktop-width) {
    padding-top: 12px;
    padding-right: 100px;
    grid-column: 2 / -1;
    grid-row: 2 / -1;
    width: auto;
  }
}


.programs__description-item {
  h3 {
    margin: 0;
    margin-bottom: 17px;
    font-family: "Montserrat", "Arial", sans-serif;
    font-weight: 700;
    color: $blue;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: -0.5px;

    @media (min-width: $desktop-width) {
      font-size: 28px;
      line-height: 34px;
      margin-bottom: 24px;
    }
  }

  span {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: $black;

    &:first-of-type {
      margin-bottom: 15px;
    }

    @media (min-width: $desktop-width) {
      font-size: 15px;
      line-height: 26px;
    }
  }
}
